.login-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.login-card {
  width: 100%;
  max-width: 500px;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.2);
}

.login-card h1 {
  text-align: center;
  margin-bottom: 25px;
  color: #333;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #555;
}

.form-group input {
  width: 100%;
  padding: 12px;
  border: 2px solid #ddd;
  border-radius: 5px;
  font-size: 16px;
  outline: none;
  transition: border-color 0.3s;
}

.form-group input:focus {
  border-color: #4285f4;
}

.login-button {
  width: 100%;
  padding: 15px;
  background-color: #4285f4;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 16px;
}

.login-button:hover {
  background-color: #3367d6;
}

.login-button:disabled {
  background-color: #a0c4f4;
  cursor: not-allowed;
}

.error-message {
  color: #e53935;
  text-align: center;
  margin-bottom: 15px;
  font-weight: 500;
}

.login-footer {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.login-footer a {
  color: #4285f4;
  text-decoration: none;
  font-size: 14px;
}

.login-footer a:hover {
  text-decoration: underline;
}

/* RTL Support */
[dir="rtl"] .login-card {
  text-align: right;
}

[dir="rtl"] .form-group label,
[dir="rtl"] .form-group input,
[dir="rtl"] .login-footer {
  text-align: right;
}