.admin-panel {
  background: rgba(255, 255, 255, 0.95);
  padding: 40px;
  border-radius: 12px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.3);
  text-align: center;
  max-width: 800px;
  width: 100%;
}

.admin-panel h1 {
  font-size: 2.2rem;
  margin-bottom: 25px;
  color: #333;
}

.admin-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.admin-form input,
.admin-form textarea {
  padding: 10px;
  border-radius: 8px;
  border: 1px solid #aaa;
  font-size: 1rem;
  width: 100%;
}

.translation-block {
  border: 1px solid #ccc;
  border-radius: 8px;
  padding: 15px;
  background-color: #f9f9f9;
}

.translation-block h3 {
  margin-bottom: 10px;
  color: #444;
}

.submit-btn {
  background-color: #2e7d32;
  color: white;
  padding: 12px;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: #1b5e20;
}

.admin-actions {
  margin-top: 20px;
}
